.App {
  text-align: center;
}

.App-header {
  background-color: #1e0d36;
  display: flex; /* Ensures flexbox layout */
  flex-direction: row; /* Keeps items in a row */
  align-items: flex-start; /* Align items to the start, allowing natural height */
  justify-content: center; /* Center items horizontally */
  font-size: calc(10px + 2vmin);
  color: white;
  width: 100%; /* Full width of the container */
  padding: 20px 0; /* Add some padding for vertical spacing */
}

.half-rounded-image {
  width: 100%; /* Adjust as needed */
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
}

.publication-list {
  /* Example styling */
  margin: 20px 0;
}

.publication-item {
  /* Example styling */
  margin-bottom: 15px;
}

.publication-list, .publication-item {
  text-align: left; /* Ensures text within these elements aligns left */
}

.App-link {
  color: #61dafb;
}
